import { Component, Element, h, Host, Listen, Prop, State } from '@stencil/core';
import { BUTTON_SIZES, BUTTON_TYPE, BUTTON_VARIANTS } from '../button/exports';
import { ICON_SIZES } from '../icons/exports';
import { INPUT_TYPES } from '../input/export';
import { TABLE_BODY_VARIANTS } from './export';
import { TILE_VARIANTS } from './tile/export';

const languages = {
  en: 'en-EN',
  de: 'de-DE',
  fr: 'fr-FR',
  it: 'it-IT',
} as const;

const weekdaysByLanguage: Record<keyof typeof languages, string[]> = {
  en: ['mo', 'tu', 'we', 'th', 'fr', 'sa', 'su'],
  de: ['mo', 'di', 'mi', 'do', 'fr', 'sa', 'so'],
  fr: ['lu', 'ma', 'me', 'je', 've', 'sa', 'di'],
  it: ['lu', 'ma', 'me', 'gi', 've', 'sa', 'do'],
};

@Component({
  tag: 'mdx-date-picker',
  styleUrl: 'date-picker.scss',
  shadow: true,
})
export class DatePicker {
  @Prop()
  cancelButtonLabel: string;

  @Prop()
  submitButtonLabel: string;

  @State()
  selectedMode: 'month' | 'year' | 'date' = 'date';

  @Prop()
  monthModeTitle: string;

  @Prop()
  yearModeTitle: string;

  @Prop()
  returnButtonLabel: string;

  @Prop()
  inputLabel: string;

  @State()
  selectedMonthView = new Date().getMonth();

  @Prop()
  language: keyof typeof languages = 'de';

  @State()
  selectedYearView = new Date().getFullYear();

  @State()
  value: Date = new Date();

  @Prop({ reflect: true })
  open?: boolean = false;

  @Element()
  host: HTMLMdxDatePickerElement;

  @Prop({ reflect: true })
  variant: (typeof TABLE_BODY_VARIANTS)[keyof typeof TABLE_BODY_VARIANTS] = TABLE_BODY_VARIANTS.DATE;

  @State()
  years: number[] = [];

  @Prop()
  arrowUpAriaLabel!: string;

  @Prop()
  arrowDownAriaLabel!: string;

  @Listen('selectTile')
  selectTileHandler({ detail }: CustomEvent<{ content: string; subContent: string; selected: boolean; tileValue: number }>) {
    if (this.selectedMode === 'month') {
      this.selectedMonthView = detail.tileValue;
      this.value.setMonth(this.selectedMonthView);
      this.selectedMode = 'date';
    } else if (this.selectedMode === 'year') {
      this.selectedYearView = detail.tileValue;
      this.value.setFullYear(this.selectedYearView);
      this.selectedMode = 'date';
    } else {
      this.value = new Date();
      this.value.setDate(detail.tileValue);
      this.value.setMonth(this.selectedMonthView);
      this.value.setFullYear(this.selectedYearView);
    }
  }

  @Listen('arrowClick')
  arrowClickHandler({ detail }: CustomEvent<'up' | 'down'>) {
    if (detail === 'up' && this.selectedMode === 'date') {
      if (this.selectedMonthView === 0) {
        this.selectedMonthView = 11;
        this.selectedYearView--;
      } else this.selectedMonthView--;
    } else if (detail === 'down' && this.selectedMode === 'date') {
      if (this.selectedMonthView === 11) {
        this.selectedMonthView = 0;
        this.selectedYearView++;
      } else this.selectedMonthView++;
    } else if (detail === 'up' && this.selectedMode === 'month') {
      this.selectedYearView--;
    } else if (detail === 'down' && this.selectedMode === 'month') {
      this.selectedYearView++;
    } else if (detail === 'up' && this.selectedMode === 'year') {
      this.selectedYearView -= 16;
      this.updateYears();
    } else if (detail === 'down' && this.selectedMode === 'year') {
      this.selectedYearView += 16;
      this.updateYears();
    }
  }

  @Listen('inputChange')
  inputChangeHandler({ detail }: CustomEvent<{ input: string }>) {
    const date = new Date(detail.input);
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();

    if (year >= 1000 && year <= 2200 && month >= 1 && month <= 12 && day >= 1 && day <= 31) {
      this.selectedYearView = year;
      this.value = date;
      this.selectedMonthView = month - 1;
    } else {
      return;
    }
  }

  @Listen('keydown', { target: 'window' })
  handleKeyDown(event: KeyboardEvent) {
    if (event.key === ' ' && this.host.shadowRoot?.activeElement?.tagName === 'MDX-INPUT') {
      event.preventDefault();
      this.open = true;
    } else if (event.key === 'Escape') {
      this.open = false;
    }
  }

  private generateMonthDays(month: number, year: number): { date: number; shortWeekday: string; fullWeekday: string }[] {
    const days: { date: number; shortWeekday: string; fullWeekday: string }[] = [];
    const date = new Date(year, month, 1);

    while (date.getMonth() === month) {
      const day = date.getDate();
      const shortWeekday = date
        .toLocaleString(languages[this.language], { weekday: 'short' })
        .substring(0, 2)
        .toLocaleLowerCase();

      const fullWeekday = date.toLocaleString(languages[this.language], { weekday: 'long' });

      days.push({ date: day, shortWeekday, fullWeekday });

      date.setDate(day + 1);
    }

    return days;
  }

  private generateMonthsAndYears(
    year: number,
  ): { shortMonthName: string; year: number; monthNumber: number; fullMonthName: string }[] {
    const months: { shortMonthName: string; year: number; monthNumber: number; fullMonthName: string }[] = [];
    const date = new Date(year, 0, 1);

    while (date.getFullYear() === year) {
      const monthWithoutTime = new Date(date.getFullYear(), date.getMonth(), date.getDate());
      const shortMonthName = monthWithoutTime.toLocaleString(this.language, { month: 'short' });
      const fullMonthName = monthWithoutTime.toLocaleString(this.language, { month: 'long' });
      months.push({
        shortMonthName,
        year: date.getFullYear(),
        monthNumber: date.getMonth(),
        fullMonthName,
      });

      date.setMonth(date.getMonth() + 1);
    }

    return months;
  }

  private generateYears(year: number, numberOfYears: number): number[] {
    const date = new Date(year, 0, 1);
    const years: number[] = [];

    for (let i = 0; i < numberOfYears; i++) {
      years.push(date.getFullYear());
      date.setFullYear(date.getFullYear() + 1);
    }

    return years;
  }

  private formatDateForInput(date: Date): string {
    const day = date.getDate().toString().padStart(2, '0');
    const month = (date.getMonth() + 1).toString().padStart(2, '0');
    const year = date.getFullYear();

    return `${year}-${month}-${day}`;
  }

  componentDidRender() {
    if (this.selectedMode === 'year' || this.selectedMode === 'month') {
      const element = this.host.shadowRoot?.querySelector('mdx-date-picker-header')?.shadowRoot?.querySelector('button');
      if (element) {
        element.tabIndex = 0;
        element.focus();
      }
    }
  }
  componentWillLoad() {
    // do not animate on render (only on click)
    if (!this.open) {
      this.open = undefined;
    }

    this.updateYears();
  }

  componentWillUpdate() {
    this.updateYears();
  }

  private updateYears() {
    this.years = this.generateYears(this.selectedYearView, 16);
  }

  private chunkArray<T>(array: T[], chunkSize: number): T[][] {
    const chunks: T[][] = [];
    for (let i = 0; i < array.length; i += chunkSize) {
      chunks.push(array.slice(i, i + chunkSize));
    }

    return chunks;
  }

  private getDates() {
    const dates = this.generateMonthDays(this.selectedMonthView, this.selectedYearView);
    const empty =
      dates.length > 0
        ? [...Array(weekdaysByLanguage[this.language].indexOf(dates[0].shortWeekday)).keys()].map(() => null)
        : [];
    const chunkedDates = this.chunkArray([...empty, ...dates], 7);

    return chunkedDates.map((chunk) => (
      <tr key={chunk.join('-')} class="date-picker__table-row-dates">
        {chunk.map((date) => {
          if (date === null) {
            return <mdx-date-picker-tile variant={TILE_VARIANTS.EMPTY} slot="tile" />;
          }

          return (
            <mdx-date-picker-tile
              key={date.shortWeekday}
              variant={TILE_VARIANTS.DATE}
              slot="tile"
              content={date.date.toString()}
              tileValue={date.date}
              today={
                date.date === new Date().getDate() &&
                this.selectedMonthView === new Date().getMonth() &&
                this.selectedYearView === new Date().getFullYear()
              }
              selected={
                date.date === this.value.getDate() &&
                this.selectedMonthView === this.value.getMonth() &&
                this.selectedYearView === this.value.getFullYear()
              }
            />
          );
        })}
      </tr>
    ));
  }

  private getMonths() {
    return this.chunkArray(this.generateMonthsAndYears(this.selectedYearView), 3).map((chunk) => (
      <tr key={chunk.join('-')} class="date-picker__table-row-month">
        {chunk.map((month) => (
          <mdx-date-picker-tile
            key={month.shortMonthName}
            variant={TILE_VARIANTS.MONTH}
            slot="tile"
            content={month.shortMonthName}
            subContent={month.year.toString()}
            tileValue={month.monthNumber}
            today={month.monthNumber === new Date().getMonth() && month.year === new Date().getFullYear()}
            selected={month.monthNumber === this.value.getMonth() && month.year === this.value.getFullYear()}
          />
        ))}
      </tr>
    ));
  }

  private getYears() {
    return this.chunkArray(this.years, 4).map((chunk) => (
      <tr key={chunk.join('-')} class="date-picker__table-row-year">
        {chunk.map((year) => (
          <mdx-date-picker-tile
            key={year}
            variant={TILE_VARIANTS.YEAR}
            slot="tile"
            content={year.toString()}
            tileValue={year}
            today={year === new Date().getFullYear()}
            selected={year === this.value.getFullYear()}
          />
        ))}
      </tr>
    ));
  }

  render() {
    return (
      <Host>
        <mdx-input
          label={this.inputLabel}
          type={INPUT_TYPES.DATE}
          value={this.formatDateForInput(this.value)}
          onClick={() => (this.open = !this.open)}
        >
          <button
            onClick={() => (this.open = !this.open)}
            slot="icon"
            class="date-picker__input-icon-button"
            aria-hidden={true}
            tabindex={-1}
          >
            <mdx-icon-calendar slot="icon" size={ICON_SIZES.SM} />
          </button>
        </mdx-input>

        <div
          class={`date-picker${this.open === undefined ? ' date-picker--is-hidden' : this.open ? ' date-picker--animate-in' : ' date-picker--animate-out'}`}
          aria-hidden={true}
        >
          {this.selectedMode === 'date' ? (
            <mdx-date-picker-header arrowUpAriaLabel={this.arrowUpAriaLabel} arrowDownAriaLabel={this.arrowDownAriaLabel}>
              <button
                slot="title"
                data-testid="select-month"
                onClick={() => (this.selectedMode = 'month')}
                class="date-picker__header-button"
              >
                {new Date(this.selectedYearView, this.selectedMonthView, 1).toLocaleString(this.language, {
                  month: 'long',
                })}
                <mdx-icon-chevron-down size={ICON_SIZES.XS} class="date-picker__header-arrow-icon" />
              </button>
              <button
                slot="title"
                data-testid="select-year"
                onClick={() => {
                  this.selectedMode = 'year';
                }}
                class="date-picker__header-button"
              >
                {this.selectedYearView}
                <mdx-icon-chevron-down size={ICON_SIZES.XS} class="date-picker__header-arrow-icon" />
              </button>
            </mdx-date-picker-header>
          ) : (
            <mdx-date-picker-header>
              <span slot="title" class="date-picker__header-title">
                {this.selectedMode === 'month' ? this.monthModeTitle : this.yearModeTitle}
              </span>
            </mdx-date-picker-header>
          )}
          {this.selectedMode === 'date' ? (
            <table class="date-picker__table">
              <thead class="date-picker__table-heading">
                {weekdaysByLanguage[this.language].map((weekday) => (
                  <mdx-date-picker-tile variant={TILE_VARIANTS.WEEKDAY} slot="tile" content={weekday} />
                ))}
              </thead>
              <tbody class="date-picker__table-body">{this.getDates()}</tbody>
            </table>
          ) : this.selectedMode === 'month' ? (
            <table class="date-picker__table">
              <tbody class="date-picker__table-body-month">{this.getMonths()}</tbody>
            </table>
          ) : (
            <table class="date-picker__table">
              <tbody class="date-picker__table-body-year">{this.getYears()}</tbody>
            </table>
          )}
          {this.selectedMode === 'date' ? (
            <mdx-date-picker-footer>
              <mdx-button-group slot="footer">
                <mdx-button
                  variant={BUTTON_VARIANTS.PRIMARY}
                  size={BUTTON_SIZES.SM}
                  onClick={() => (this.open = false)}
                  type={BUTTON_TYPE.SUBMIT}
                >
                  {this.submitButtonLabel}
                </mdx-button>
                <mdx-button
                  variant={BUTTON_VARIANTS.TERTIARY}
                  size={BUTTON_SIZES.SM}
                  onClick={() => (this.open = false)}
                  type={BUTTON_TYPE.RESET}
                >
                  {this.cancelButtonLabel}
                </mdx-button>
              </mdx-button-group>
            </mdx-date-picker-footer>
          ) : (
            <mdx-date-picker-footer>
              <mdx-button
                slot="footer"
                variant={BUTTON_VARIANTS.TERTIARY}
                size={BUTTON_SIZES.SM}
                onClick={() => (this.selectedMode = 'date')}
              >
                <mdx-icon-arrow-left size={ICON_SIZES.SM} />
                <span>{this.returnButtonLabel}</span>
              </mdx-button>
            </mdx-date-picker-footer>
          )}
        </div>
      </Host>
    );
  }
}
