:host {
  position: relative;
}

:host .date-picker {
  position: absolute;
  width: var(--mdx-comp-date-picker-container-sizing-width);
  height: var(--mdx-comp-date-picker-container-sizing-height);
  box-shadow: var(--mdx-comp-date-picker-container-shadow-default);
  border-radius: var(--mdx-comp-date-picker-container-border-radius-default);
  top: 100%;
  background-color: var(--mdx-comp-date-picker-header-background-color-default);
  z-index: 10;

  &--animate-in {
    animation: animateIn var(--mdx-comp-date-picker-transition-fade-in-out-duration)
      var(--mdx-comp-date-picker-transition-fade-in-out-timing-function)
      var(--mdx-comp-date-picker-transition-fade-in-out-delay);
    animation-fill-mode: forwards;
  }

  &--animate-out {
    animation: animateOut var(--mdx-comp-date-picker-transition-fade-in-out-duration)
      var(--mdx-comp-date-picker-transition-fade-in-out-timing-function)
      var(--mdx-comp-date-picker-transition-fade-in-out-delay);
    animation-fill-mode: forwards;
  }

  &--is-hidden {
    display: none;
  }

  &__header-button {
    transition: var(--mdx-comp-icon-button-transition-hover-fade-in-out);
    transition-property: background-color;

    &:focus-visible {
      border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
      outline-color: var(--mdx-comp-keyboard-focus-color-default);
      outline-style: solid;
      outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
      outline-offset: 2px;
    }
    &:hover {
      background-color: var(--mdx-comp-icon-button-tertiary-small-background-color-hover);
    }
  }

  &__header-arrow-icon {
    color: var(--mdx-comp-date-picker-header-icon-color-default);
  }

  &__table-heading {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
  }

  &__table {
    padding: var(--mdx-comp-date-picker-container-padding-top-bottom)
      var(--mdx-comp-date-picker-container-padding-left-right);
  }

  &__table-body {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
  }

  &__table-body-month {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: var(--mdx-comp-date-picker-tile-list-spacing-gap-month-vertical);
  }

  &__table-body-year {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: var(--mdx-comp-date-picker-tile-list-spacing-gap-year-horizontal);
  }

  &__table-row-dates {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }

  &__table-row-month {
    display: flex;
    justify-content: center;
    column-gap: var(--mdx-comp-date-picker-tile-list-spacing-gap-month-horizontal);
  }

  &__table-row-year {
    display: flex;
    justify-content: center;
    column-gap: var(--mdx-comp-date-picker-tile-list-spacing-gap-year-horizontal);
  }

  &__header-title {
    pointer-events: none;
  }

  &__input-icon-button {
    all: unset;
    cursor: pointer;
    color: var(--mdx-comp-inputfield-icon-color-default);

    &:focus-visible {
      border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
      outline-color: var(--mdx-comp-keyboard-focus-color-default);
      outline-style: solid;
      outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
      outline-offset: 2px;
    }
  }
}

@media screen and (max-width: 768px) {
  :host .date-picker {
    display: none;

    &__input-icon-button {
      display: none;
    }
  }
}

@keyframes animateIn {
  from {
    opacity: 0;
    display: block;
  }
  to {
    opacity: 1;
  }
}

@keyframes animateOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    display: none;
  }
}
